import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';

const styles = () => ({
  button: {
    fontFamily: 'inherit',
    borderRadius: '10px',
  },
  formControl: {
    minWidth: 300,
    fontFamily: 'inherit',
  },
  inputLabel: {
    fontFamily: 'inherit',
  },
});

class Search extends React.Component {
  state = {
    query: '',
    open: false,
    param: '',
    age:''
  };

  handleChange = (event) => {
    console.log(event)
    //event.preventDefault();
    this.setState({ param: event.target.value });
    this.props.getCollectionByQueryParams(event.target.value);
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  handleInputChange = () => {
    this.setState({
      query: this.search.value,
    });
  };

  submit = (event) => {
    event.preventDefault();
    this.props.getCollectionByQueryParams(this.search.value);
  };

  sortCollection = (event) => {
    event.preventDefault();
    console.log(event)
    this.props.sortCollection();
  }

  render() {
    return (
      <div className="search-container clearfix">
        <div className="filter">
          <form autoComplete="off">
            <FormControl className={this.props.classes.formControl}>
              <InputLabel className={this.props.classes.inputLabel} htmlFor="demo-controlled-open-select">
                Order by:
              </InputLabel>
              <Select
                open={this.state.open}
                onClose={this.handleClose}
                onOpen={this.handleOpen}
                value={this.state.param}
                onChange={this.handleChange}
                inputProps={{
                  id: 'demo-controlled-open-select',
                }}
              >
                <MenuItem value="relevance">Relevance</MenuItem>
                <MenuItem value="objecttype">Object type</MenuItem>
                <MenuItem value="chronologic">Chronologic</MenuItem>
                <MenuItem value="achronologic">Achronologic</MenuItem>
                <MenuItem value="artist">Artist</MenuItem>
                <MenuItem value="artistdesc">Artist desc</MenuItem>
              </Select>
            </FormControl>
          </form>
        </div>
        <div className="form">
          <form onSubmit={this.submit}>
            <input
              placeholder="Search keyword..."
              ref={(input) => (this.search = input)}
              onChange={this.handleInputChange}
            />
            <Button className={this.props.classes.button} type="submit">
              Search
            </Button>
          </form>
        </div>
      </div>
    );
  }
}

Search.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Search);
