import React from 'react';
import PropTypes from 'prop-types';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';

import { withStyles } from '@material-ui/core/styles';

const styles = (theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 1200,
    height: 450,
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  gridListTileBar: {
    display: 'none',
  },
  gridListTileBarNone: {
    display: 'flex',
    cursor: 'pointer',
    '& div': { fontSize: '10px', whiteSpace: 'normal', lineHeight: '13px' },
  },

  img: {
    height: '104px',
  },
  title: {
    fontSize: '10px',
    whiteSspace: 'normal',
  },
  popUp: {
    display: 'none'
  }
});

class Picture extends React.Component {
  state = {
    isHovered: false,
    isShown: false
  };

  hadleHover = () => {
    this.setState({
      isHovered: !this.state.isHovered,
    });
  };

  showPicture = () => {
    console.log('click');
    this.setState({
      isShown: true,
    });
  }


  render() {
    const btnClass = this.state.isHovered ? this.props.classes.gridListTileBarNone : this.props.classes.gridListTileBar;
    const popUp = this.state.isShown ? this.props.classes.gridListTileBarNone : this.props.classes.gridListTileBar;
    return (
      <React.Fragment>
        <div onClick={() => this.showPicture} onMouseEnter={this.hadleHover} onMouseLeave={this.hadleHover}>
          <img src={this.props.picture.headerImage.url} className={this.props.classes.img} />
          <GridListTileBar className={btnClass} title={this.props.picture.longTitle} />
        </div>

        <div className={popUp}>
          jhljhljhjkh;
        </div>
      </React.Fragment>
    );
  }
}

Picture.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Picture);
