import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import GridList from '@material-ui/core/GridList';


import Picture from './Picture';

const styles = (theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 1200,
    height: 450,
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  gridListTileBar: {
    display: 'none',
  },
  gridListTileBarNone: {
    display: 'flex',
    cursor: 'pointer',
  },
});

class PictureList extends React.Component {
  constructor(props) {
    super(props);
  }

  state = {
    isShown: false
  };

  showPicture = () => {
    console.log('click')
    this.setState({
      isShown: !this.state.isShown,
    });
  }

  render() {
    const popUp = this.state.isShown ? this.props.classes.gridListTileBarNone : this.props.classes.gridListTileBar;

    if (this.props && this.props.pictures) {
      return (
        <div className={this.props.classes.root}>
          <GridList spacing={20} cols={5} cellHeight={100} className={this.props.classes.gridList}>
            {this.props.pictures.map((picture) => (
              <GridListTile col={5} key={picture.id}>
                <Picture onClick={() => this.showPicture}  picture={picture} />
              </GridListTile>
            ))}
          </GridList>
        </div>
      );
    } else {
      return <div>Loading...</div>;
    }
  }
}

PictureList.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(PictureList);

