import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import PropTypes from 'prop-types';

import Header from './Header';
import PictureList from './PictureList';
import Search from './Search';
import Pagination from './Pagination';
const styles = () => ({
  progress: {
    margin: '10% 50%',
  },
});

class App extends React.Component {
  state = {
    error: null,
    collection: [],
    isLoaded: false,
    query: null,
    param: null,
    pageNumber: 1,
    pageCount: null,
  };

  componentDidMount() {
    this.getCollection();
  }

  getCollectionByQueryParams = (query) => {
    const getCollectionEndpoint =
      'https://community-rijksmuseum.p.rapidapi.com/en/collection?key=nMG7xRY4&format=json&ps=50';

    fetch(`${getCollectionEndpoint}&q=${query}`, {
      method: 'GET',
      headers: {
        'X-RapidAPI-Key': 'b48c4540ccmshee96167b8da23f3p109699jsnf8fe38cb12ae',
      },
    })
      .then((response) => {
        if (response.ok) return response.json();
        this.setState({
          error: response.statusText,
        });
      })
      .then((collection) => {
        console.log(collection);
        this.setState({
          collection,
        });
      })
      .finally(() => {
        this.setState({
          isLoaded: false,
        });
      })
      .catch((error) => console.log(error.message));
  };

  sortCollection = (param) => {
    const getCollectionEndpoint =
      'https://community-rijksmuseum.p.rapidapi.com/en/collection?key=nMG7xRY4&format=json&ps=50';

    console.log(param);

    fetch(`${getCollectionEndpoint}&q=${param}`, {
      method: 'GET',
      headers: {
        'X-RapidAPI-Key': 'b48c4540ccmshee96167b8da23f3p109699jsnf8fe38cb12ae',
      },
    })
      .then((response) => {
        if (response.ok) return response.json();
        this.setState({
          error: response.statusText,
        });
      })
      .then((collection) => {
        console.log(collection);
        this.setState({
          collection,
        });
      })
      .finally(() => {
        this.setState({
          isLoaded: false,
        });
      })
      .catch((error) => console.log(error.message));
  };

  getCollection = (pageNumber) => {
    this.setState({
      isLoaded: true,
    });

    const number = pageNumber || 1;

    console.log(number);

    const getCollectionEndpoint = `https://community-rijksmuseum.p.rapidapi.com/en/collection?key=nMG7xRY4&format=json&ps=50&p=${number}`;

    fetch(getCollectionEndpoint, {
      method: 'GET',
      headers: {
        'X-RapidAPI-Key': 'b48c4540ccmshee96167b8da23f3p109699jsnf8fe38cb12ae',
      },
    })
      .then((response) => {
        if (response.ok) return response.json();
        this.setState({
          error: response.statusText,
        });
      })
      .then((collection) => {
        const count = collection.count / 50;
        this.setState({
          collection,
          pageCount: 200,
        });
      })
      .finally(() => {
        this.setState({
          isLoaded: false,
        });
      })
      .catch((error) => console.log(error.message));

    return false;
  };

  updatePageNumber = (pageNumber) => {
    // 1. Take a copy of the current state
    //const fishes = { ...this.state.fishes };
    // 2. Update that state
    //fishes[key] = updatedFish;
    // 3. Set that to state
    this.setState({ pageNumber });
  };

  onChangePageSize = (pageSize) => {
    console.log('pageSize', pageSize);
  };

  render() {
    /* if (this.state.isLoaded) {
      return (
        <div>
          <CircularProgress className={this.props.classes.progress} color="secondary" />
        </div>
      );
    } else { */
    return (
      <React.Fragment>
        <Header />
        <Search
          getCollectionByQueryParams={this.getCollectionByQueryParams}
          query={this.state.query}
          sortCollection={this.sortCollection}
          param={this.state.param}
        />
        {this.state.collection.artObjects && this.state.collection.artObjects.length !== 0 ? (
          <PictureList pictures={this.state.collection.artObjects} />
        ) : (
          <div className="no-found">No art object could be found by your query</div>
        )}
        <Pagination
          pageNumber={this.state.pageNumber}
          updatePageNumber={this.updatePageNumber}
          getCollection={this.getCollection}
          pageCount={this.state.pageCount}
          onChangePageSize={this.onChangePageSize}
        />
      </React.Fragment>
    );
    //}
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);
